import { Component, OnInit } from '@angular/core';
import { LotteryService } from 'src/app/services/lottery.service';
import { AccountService } from 'src/app/services/account.service';
import { AccountHistory } from 'src/app/models/account/account-history';
import { RewardHistory } from 'src/app/models/account/reward-history';
import { BetHistory } from 'src/app/models/account/bet-history';

@Component({
  selector: 'app-winning-history',
  templateUrl: './winning-history.component.html',
  styleUrls: ['./winning-history.component.scss']
})
export class WinningHistoryComponent implements OnInit {

  constructor(
    public lotteryService: LotteryService,
    public accountService: AccountService
  ) { }

  rewardHistory: RewardHistory[] = [];
  betHistory: BetHistory[] = [];

  rewardPageNum = 0;
  betPageNum = 0;
  isLoadingReward = false;
  isLoadingBets = false;

  ngOnInit() {
    this.isLoadingReward = true;
    this.accountService.getRewardHistory(1, 100)
      .subscribe((data: any) => {
        this.isLoadingReward = false;
        if (data.code === 200) {
          this.rewardHistory = data.msg;
        }
      });

    this.accountService.getBetsHistory(1, 100)
      .subscribe((data: any) => {
        this.isLoadingBets = false;
        if (data.code === 200) {
          this.betHistory = data.msg;
        }
      });
  }

  onRewardScroll() {
    this.rewardPageNum ++;
    this.isLoadingReward = true;
    this.accountService.getRewardHistory(this.rewardPageNum, 100)
      .subscribe((data: any) => {
        this.isLoadingReward = false;
        const arr = data.msg;

        arr.forEach((item: RewardHistory) => {
          if (!this.rewardHistory.find(dup => {
            return dup.ticket === item.ticket &&
            dup.gameid === item.gameid;
          })) {
            this.rewardHistory.push(item);
          }
        });
      });
  }

  onBetScroll() {
    this.betPageNum ++;
    this.isLoadingBets = true;
    this.accountService.getBetsHistory(this.betPageNum, 100)
      .subscribe((data: any) => {
        this.isLoadingBets = false;
        const arr = data.msg;

        arr.forEach((item: BetHistory) => {
          if (!this.betHistory.find(dup => {
            return dup.gameid === item.gameid &&
            dup.start === item.start &&
            dup.end === dup.end;
          })) {
            this.betHistory.push(item);
          }
        });
      });
  }
}
