import { Candy } from './candy';

export class WinnerInfo {
    candies: Candy[];
    preDisplayWinnings: number;
    myWinnings: number;
    isDrawing?: boolean;

    constructor(preDisplayWinnings = 0) {
        this.candies = [];
        this.preDisplayWinnings = preDisplayWinnings;
        this.myWinnings = 0;
        this.isDrawing = true;
    }
}
