export class Candy {
  candyNumber: string;
  userName: string;
  amount: number;
  portraitUrl?: string;
  isMine?: boolean;

  constructor(
    candy: string,
    userName: string,
    amount: number,
    isMine: boolean
  ) {
    this.candyNumber = candy;
    this.userName = userName;
    this.isMine = isMine;
    this.amount = amount;
  }
}
