import { Component, OnInit } from '@angular/core';
import { PayoutService } from '../services/payout.service';
import { LotteryService } from '../services/lottery.service';
import { ActivatedRoute } from '@angular/router';
import { Payout } from '../models/payout/payout';
import { PlaceMapArr } from '../constants/placeMap';
import { LastGame } from '../models/lottery/last-game';

@Component({
  selector: 'app-payout',
  templateUrl: './payout.component.html',
  styleUrls: ['./payout.component.scss']
})
export class PayoutComponent implements OnInit {

  constructor(
    private route: ActivatedRoute,
    public lotteryService: LotteryService,
    public payoutService: PayoutService
  ) { }

  payoutData: {
    [key: string]: {
      data: Payout[];
      page: number;
      isEnded: boolean;
    }
  } = {};

  payoutList: string[];

  placeMap = PlaceMapArr;

  rankingMap = {
    first: 0,
    second: 0,
    third: 0
  };

  lastTenGames: LastGame[] = [];

  selectedGame: LastGame;

  currentLevel = '1';

  ngOnInit() {
    this.route.queryParams.subscribe((params) => {
      this.currentLevel = params.level || '1';
      this.initialize();
    });
  }

  initialize() {
    this.lotteryService.getLastTenGames()
      .subscribe((data: any) => {
        if (data && data.msg) {
          this.lastTenGames = data.msg;
        }
        if (this.lastTenGames && this.lastTenGames.length) {
          this.selectedGame = this.lastTenGames[0];
        }
        this.getData(this.currentLevel);
      });
  }

  hasPreviousGame() {
    if (this.lastTenGames &&
      this.lastTenGames.length &&
      this.lastTenGames.indexOf(this.selectedGame) !== -1 &&
      this.lastTenGames[this.lastTenGames.indexOf(this.selectedGame) + 1])
    {
      return this.lastTenGames[this.lastTenGames.indexOf(this.selectedGame) + 1];
    }
    return null;
  }

  getPreviousGame() {
    const newGame = this.hasPreviousGame();

    if (newGame) {
      this.selectedGame = newGame;
      this.currentLevel = '1';
      this.payoutData = {};
      this.payoutData[this.currentLevel] = {
        data: [],
        page: 1,
        isEnded: false
      };

      this.getData(this.currentLevel);
    }
  }

  hasNextGame() {
    if (this.lastTenGames &&
      this.lastTenGames.length &&
      this.lastTenGames.indexOf(this.selectedGame) > 0 &&
      this.lastTenGames[this.lastTenGames.indexOf(this.selectedGame) - 1]) {
      return this.lastTenGames[this.lastTenGames.indexOf(this.selectedGame) - 1];
    }
    return null;
  }

  getNextGame() {
    const newGame = this.hasNextGame();

    if (newGame) {
      this.selectedGame = newGame;
      this.currentLevel = '1';
      this.payoutData = {};
      this.payoutData[this.currentLevel] = {
        data: [],
        page: 1,
        isEnded: false
      };

      this.getData(this.currentLevel);
    }
  }

  selectRank(rank) {
    this.currentLevel = rank;
    if (!this.payoutData[rank]) {
      this.getData(rank);
    }
  }

  getData(level) {
    if (!this.selectedGame) {
      return;
    }

    if (!this.payoutData[level]) {
      this.payoutData[level] = {
        data: [],
        page: 1,
        isEnded: false
      };
    }

    if (this.lastTenGames && this.lastTenGames.length && this.selectedGame) {
      this.payoutService.getPayoutHistory(
        this.selectedGame.id.toString(),
        level,
        this.payoutData[level].page,
        100
      )
        .subscribe((data: any) => {
          this.payoutData[level].page ++;
          this.payoutData[level].data = data.msg;
          this.payoutData[level].data.forEach(item => {
            this.setPlaceMap(item.prize);
          });
        });
    } else {
      setTimeout(() => {
        this.getData(level);
      }, 100);
    }
  }

  getPlace(amount) {
    switch (amount) {
      case this.rankingMap.first:
        return 'first';
      case this.rankingMap.second:
        return 'second';
      case this.rankingMap.third:
        return 'third';
      default:
        return '';
    }
  }

  setPlaceMap(amount) {
    if (amount === this.rankingMap.first ||
      amount ===  this.rankingMap.second ||
      amount === this.rankingMap.third ||
      amount < this.rankingMap.third) {
        return;
      }

    if (amount > this.rankingMap.first) {
      this.rankingMap.third = this.rankingMap.second;
      this.rankingMap.second = this.rankingMap.first;
      this.rankingMap.first = amount;
    } else if (amount > this.rankingMap.second) {
      this.rankingMap.third = this.rankingMap.second;
      this.rankingMap.second = amount;
    } else {
      this.rankingMap.third = amount;
    }
  }

  onScroll() {
    this.payoutData[this.currentLevel].page ++;
    this.payoutService.getPayoutHistory(
      this.lotteryService.lastGameId,
      this.currentLevel,
      this.payoutData[this.currentLevel].page,
      100
    )
      .subscribe((data: any) => {
        const arr = data.msg;

        arr.forEach(item => {
          if (!this.payoutData[this.currentLevel].data.find(dup => {
            return dup.user === item.user &&
            dup.candy === item.candy &&
            dup.time === item.time;
          })) {
            this.payoutData[this.currentLevel].data.push(item);
          }
        });
      });
  }
}
